/* HEADER */

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.header {
  overflow: hidden;
  padding: 16px 10px;
}

.header a {
  float: left;
  text-align: center;
  padding: 14px;
  text-decoration: none;
  font-size: 18px;
  line-height: 23px;
  border-radius: 4px;
  margin-right: 10px;
}

.header a.logo {
  font-size: 22px;
}

.header a:hover {
  background-color: #ddd;
}

.header a.active {
  background-color: rgb(110, 172, 125);
  color: white;
}

.header-right {
  float: right;
}

@media screen and (max-width: 550px) {
  .header a {
    padding: 8px;
    font-size: 12px;
    line-height: 15px;
    margin-right: 2px;
  }
  .header a.logo {
    font-size: 18px;
    font-weight: bold;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Noto Sans TC", sans-serif;
}

.navbar {
  background-color: #187ac8;
}

.nav-wrap {
  display: flex;
  flex-wrap: wrap;
  padding: 1rem 0 0;
}

.nav-wrap a {
  text-decoration: none;
  color: white;
}

.nav-wrap ul {
  display: flex;
  display: none;
  width: 100%;
  list-style-type: none;
  border-top: 1px rgba(255, 255, 255, 0.4) solid;
}

.nav-wrap ul li {
  width: 50%;
  display: flex;
}

.nav-wrap ul li a {
  text-align: center;
  width: 100%;
  padding: 1rem 0;
  transition: all 0.25s;
}

.nav-wrap ul li a:hover {
  color: #f78c00;
}

.title {
  margin-left: 1rem;
  margin-bottom: 1rem;
}

.toggle-btn {
  margin: 0.75rem 1rem 0 auto;
}

.hamburger {
  background-color: white;
  width: 32px;
  height: 2px;
  position: relative;
  transition: all 0.2s ease-in-out;
}

.hamburger::before {
  content: "";
  background-color: white;
  width: 32px;
  height: 2px;
  position: absolute;
  top: 10px;
}

.hamburger::after {
  content: "";
  background-color: white;
  width: 32px;
  height: 2px;
  position: absolute;
  top: 20px;
}

.toggled {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  top: 10px;
}

.toggled::before {
  opacity: 0;
}

.toggled::after {
  -webkit-transform: rotate(90deg) translate(-20px);
          transform: rotate(90deg) translate(-20px);
}

.footer {
  background-color: #187ac8;
  padding: 0.75rem 0;
}

.footer p {
  text-align: center;
  padding: 5px;
}

.map .actived {
  fill: rgba(255, 255, 255, 0.4);
}

.map .actived path {
  fill: rgba(255, 255, 255, 0.4);
}

.map path {
  stroke: #197ac9;
  stroke-width: 2px;
  fill: #e6f8fc;
  transition: all 0.2s linear;
}

.map path:hover {
  fill: rgba(255, 255, 255, 0.4);
  cursor: pointer;
}

@media screen and (min-width: 576px) {
  .toggle-btn {
    display: none;
  }
  .title {
    margin-top: 1rem;
  }
  .nav-wrap {
    padding: 0;
  }
  .nav-wrap ul {
    display: flex;
    width: 30%;
    border-top: none;
    margin: 0 1rem 0 auto;
  }
  .nav-wrap ul li {
    width: 100%;
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .nav-wrap {
    max-width: 768px;
    margin: auto;
  }
}

@media screen and (min-width: 992px) {
  .nav-wrap {
    max-width: 992px;
    margin: auto;
  }
}

@media screen and (min-width: 1200px) {
  .nav-wrap {
    max-width: 1200px;
    margin: auto;
  }
}

