.airGreen {
  -webkit-animation: airGreen 2s ease-in-out forwards;
          animation: airGreen 2s ease-in-out forwards;
}

@-webkit-keyframes airGreen {
  0% {
    width: 0;
  }
  100% {
    width: calc(100% / 10 * 1.7);
    background-color: #00e800;
  }
}

@keyframes airGreen {
