  }
}

.airBrown {
  -webkit-animation: airBrown 1.5s ease-in-out forwards;
          animation: airBrown 1.5s ease-in-out forwards;
}

@-webkit-keyframes airBrown {
  0% {
    width: 0;
  }
  17% {
    background-color: #00e800;
  }
  34% {
    background-color: #ffeb3b;
  }
  51% {
    background-color: #f39800;
  }
  68% {
    background-color: #ea1904;
  }
  75% {
    background-color: #b34fa2;
  }
  100% {
    width: calc(100% / 10 * 10);
    background-color: #492042;
  }
}

@keyframes airBrown {
  0% {
    width: 0;
  }
  17% {
    background-color: #00e800;
  }
